<template>
    <s-header :name="'健康追踪'" :rightname="'编辑卡片'" :rightclick="rightclick"></s-header>
    <div style="background-color: #F2F4F8;overflow-y: scroll;height: calc(100vh - 44px)">
        <div class="item" v-for="item in todos" :key="item.id" @click="item.click">
            <div style="display: flex;align-items: center;flex: 2;">
                <svg aria-hidden="true" style="width: 14px;justify-content: center;align-items: center" v-if="!isback"  @click="goBack">
                    <use :xlink:href="item.icon"></use>
                </svg>
                <p class="title">{{item.text}}</p>
            </div>
            <div style="flex: 3">
                <p class="data">{{item.text}}</p>
                <p class="date">{{item.text}}</p>
            </div>

        </div>
    </div>
</template>

<script>
  import sHeader from '@/components/SimpleHeader'
  import {reactive, computed, onMounted,} from 'vue'
  import { useRouter } from 'vue-router'

  export default {
    name: "index",
    components: {
      sHeader
    },
    setup() {
      const router = useRouter()

      onMounted(
        () => {
        }
      )

      // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
      const state = reactive({
        todoList: [{
          id: 1,
          text: '饮水',
          icon:'#icon-heshui',
          click:()=>{router.push({ path: '/drink'})},
        }, {
          id: 2,
          text: '膳食',
          icon:'#icon-_shanshijiankang',
          click:()=>{router.push({ path: '/food'})},

        }, {
          id: 3,
          text: '睡眠',
          icon:'#icon-shuimian',
          click:()=>{router.push({ path: '/sleep'})},

        },{
          id: 4,
          text: '运动',
          icon:'#icon-paobu',
          click:()=>{router.push({ path: '/motion'})},

        },{
          id: 5,
          text: '血压',
          icon:'#icon-xueya',
          click:()=>{router.push({ path: '/bloodpressure'})},

        },{
          id: 6,
          text: '心率',
          icon:'#icon-xinlv',
          click:()=>{router.push({ path: '/heartrate'})},

        },{
          id: 7,
          text: '血糖',
          icon:'#icon-xuetang',
          click:()=>{router.push({ path: '/bloodsugar'})},

        },{
          id: 8,
          text: '体重/体脂',
          icon:'#icon-chengtizhong',
          click:()=>{router.push({ path: '/weight-height'})},

        },{
          id: 9,
          text: '血氧饱和度',
          icon:'#icon-chengtizhong',
          click:()=>{router.push({ path: '/bloodOxygenData/blod'})},
        },
        {
          id: 10,
          text: '体温',
          icon:'#icon-chengtizhong',
          click:()=>{router.push({ path: '/bloodOxygenData/temperature'})},
        }
        ],
        todo: ''
      })
      // 使用计算属性生成待办列表
      const todos = computed(() => {
        return state.todoList.filter(item => !item.done)
      })

      const rightclick = () =>{
        router.push({ path: '/EditCard'})
      }

      // 在Vue3.0中，所有的数据和方法都通过在setup 中 return 出去，然后在template中使用
      return {
        state,
        todos,
        rightclick,
      }
    }
  }
</script>

