    .item {
        background: #FFFFFF;
        box-shadow: 0 15px 9px -7px #E5EAF5;
        border-radius: 5px;
        border-radius: 5px;
        height: 80px;
        align-items: center;
        display: flex;
        margin: 10px 10px 0px 10px;
        padding: 0 10px 0 10px;
        .title {
            font-family: PingFangSC-Regular;
            font-size: 18px;
            color: #333333;
            margin-left: 10px;
        }
        .data{
            font-family: PingFangSC-Medium;
            font-size: 16px;
            color: #06B48D;
        }
        .date{
            font-family: PingFangSC-Medium;
            font-size: 12px;
            color: #5ABFA8;
        }
    }
</style>
